@import '../../../../includes/assets/css/consts.scss';
@import '../base/mixins';

.dpit-theme-colored {

	// overlay
	--dpit-overlay-color: var(--accent-contrast-color);

	// highlight
	--dpit-highlight-border-color: var(--accent-contrast-inv-color);

	// number
	--dpit-number-tx-color: var(--accent-contrast-color);
	--dpit-number-bg-color: var(--accent-color-gentle);
	--dpit-number-border-color: var(--accent-contrast-inv-color);

	// animation
	--bullet-anim-color: var(--accent-contrast-inv-color);
	--bullet-anim-on-color: var(--accent-color);

	// tooltip
	--dpit-tooltip-bg-color: var(--accent-color-gentle);
	--dpit-tooltip-tx-color: var(--accent-contrast-color);

	// buttons
	--dpit-btn-tx-color: var(--accent-contrast-inv-color);
	--dpit-btn-tx-on-color: var(--accent-contrast-inv-color);
	--dpit-btn-tx-alpha: 0.85;
	--dpit-btn-bg-color: var(--accent-contrast-color);
	--dpit-btn-bg-alpha: 0.7;

	// navig bullets
	--dpit-bullet-bg-color: var(--accent-contrast-color);
	--dpit-bullet-on-bg-color: var(--accent-contrast-color);
	--dpit-bullet-on-tx-color: var(--accent-contrast-inv-color);

	//progress
	--dpit-progress-front-color: var(--accent-contrast-color);
	--dpit-progress-bg-color: var(--accent-contrast-color);


	// scrollbar
	--dpit-scrollbar-bg-color: var(--accent-contrast-color);

	&.dpit-light-accent {

		// tooltip
		--dpit-overlay-color: var(--accent-contrast-color);
		--dpit-tooltip-tx-color: var(--accent-contrast-color);
	}

	.dpit-number {
		&--on-tooltip {
			--dpit-number-border-color: var(--accent-contrast-color);
		}
	}


	.dpit-tooltip {

		.dpit-tooltip-text {
			letter-spacing: .1em;

			a {
				color: rgb(var(--dpit-tooltip-tx-color)) !important;
				text-decoration: underline;
			}
		}
	}

	&.dpit-light-accent {
		.dpit-tooltip-text {
			text-shadow: 0.2px 0px 0px rgb(var(--dpit-tooltip-tx-color));
		}

		.dpit-number {
			&--on-tooltip {
				--dpit-number-border-color: var(--accent-contrast-color);
				--dpit-btn-bg-alpha-2: 0.85;
			}
		}
	}

}